/* CV PAGE */

.PageCV {
    text-align: center;
    background-color: rgb(68, 66, 66);
    color: #fff;
    width: 100vw;
    height: 100vh;
    display: static;
    position: relative;
}

.Title {
    font-size: 2rem;
}
.Title h1{
    margin : 0px;
}


.PDF {
    position:absolute;
    height: 100vh;
    width: 100vw;
}


.embed {
    position:absolute;
    height: 100vh;
    width: 100vw;
}
